<script>
import './css/style.css'
import './css/animation.css'
export default {
  name:'APP',
  created(){
    this.navKey = this.$route.name;
    // this.$route
  },
  data (){
    return {
      navKey:'home',
      imgs:{
        facrbook:require("./img/facrbook.svg"),
        twitter:require('./img/twitter.svg'),
        instagram:require('./img/instagram.svg')
      },
      transitionName: 'slide-up'
    }
  },

  methods: {
    clickNav(key){
      console.log(key)
      this.navKey = key;
    }
  },
  watch: {
    '$route'(to, from) {
      console.log(to,from)
      if (to.name == 'home') {
        this.navKey = 'home';
      }
      const toIndex = to.meta.index;
      const fromIndex = from.meta.index;
      this.transitionName = toIndex < fromIndex ? 'slide-down' : 'slide-up'
    },
  }
};
</script>

