<script>
export default {
	data(){
		return {
			showDot:false,
			isLeave:false,
			showNumMove1: false
		}
	},
	methods: {
		showInDot(e){
			debugger
			var that = this;
			var offsetX = e.offsetX;
			var offsetY = e.offsetY;
			var span = document.createElement('span');
			span.style.position = 'absolute';
			span.style.display = 'block';
			span.style.zIndex = '10';
			span.style.width = 60 +'px';
			span.style.height = 60 +'px';
			span.style.borderRadius = '50%';
			span.style.left = (offsetX - 30) + 'px';
			span.style.top = (offsetY - 30) + 'px';
			span.style.transform = 'scale(1,1)';
			span.style.webkitTransform = 'scale(1,1)';
			span.style.background = 'aqua';
			span.style.opacity = 1;
			span.style.transition = 'all .6s linear';
			// span.style.webkitTransition = 'all 4s liner';
			e.target.parentElement.appendChild(span);
			setTimeout(()=>{
				var relese = function () {
					e.target.parentElement.removeChild(span);		
           		}
				span.style.opacity = 0;
				span.style.transform = 'scale(5,5)';
				span.style.webkitTransform = 'scale(5,5)';
				var transitionEvent = that.whichTransitionEvent();
                transitionEvent && span.addEventListener(transitionEvent, relese, false);
			},16)
		},
		whichTransitionEvent(){
            var t,
                el = document.createElement('div'),
                transitions = {
                    transition: 'transitionend',
                    OTransition: 'oTransitionEnd',
                    MozTransition: 'transitionEnd',
                    WebKitTransition:'webkitTransitionEnd'
                }
            for (t in transitions) {
                if(el.style[t] !== undefined) {
                    return transitions[t]
                }
            }
        },
	},
	mounted(){
		this.isLeave = true;
		this.showNumMove1 = true
	}
}
</script>

